.box {
    .title-list {
        .title {
            transition: color 0.1s, background-color 0.1s;

            &:hover {
                background-color: rgb(255, 193, 7) !important;
                color: black !important;
                cursor: pointer;
            }
        }
    }

    .selectedTitle {
        background-color: rgb(255, 193, 7) !important;
        color: black !important;
    }

    .content-box {
        object {
            height: 70rem;
        }
    }
}